/* App*/
body {
  margin: 0;
  padding: 0;
  color: #555;
  font-family: libre franklin, arial, sans-serif;
  line-height: 25px;
  font-weight: 400;
  background: #fff;
}

img {
  size: 50%;
}

.App {
  text-align: left;
  margin-left: 3rem;
  margin-right: 3rem;
}

.App-logo {
  /*  color: #f19b9b;
    animation: App-logo-spin infinite 20s linear;
    height: 50vmin;
  text-align: center;
  margin: auto;
  background-image: url("https://cdn.financer.com/wp-content/uploads/sites/2/financer-bg-min.jpg");
  height: 50vmin;
  background-size: cover;
  display: block;*/
  size: 3rem;
}

.App-header {
  background-color: #282c34;
  /*min-height: 100vh;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 5%;
}
.home-main-categories {
  margin: 0 auto 80px;
}

.App-link {
  color: #61dafb;
}

.App-text {
  margin-top: 4rem;
  margin-left: 4rem;
  padding: 2%;
  color: #495057;
  /*
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  */
  padding-right: 15px;
  padding-left: 15px;
  /*
    font-family: 'Pragati Narrow', sans-serif;
  font-family: 'M PLUS 1p', sans-serif;
  font-family: 'Cabin Sketch', cursive;
    */
  font-family: "M PLUS 1p", sans-serif;
}

.is-active {
  color: green;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 420px) {
  .App-text {
    font-size: 14px;
    margin-left: 4%;
  }

  .App-header h1 {
    align-items: center;
    font-size: 12px;
    color: rgb(243, 237, 237);
    font-size: calc(10px + 2vmin);
    margin-bottom: 5%;
  }

  .App {
    text-align: left;
    margin-left: 0rem;
    margin-right: 0rem;
  }

  img {
    width: 70%;
    margin-top: 10%;
  }
}
