@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@media (max-width: 420px) {
  .App-text {
    font-size: 14px;
    margin-left: 4%; }
  .App-header h1 {
    align-items: center;
    font-size: 12px;
    color: #f3eded;
    font-size: calc(10px + 2vmin);
    margin-bottom: 5%; }
  .App {
    text-align: left;
    margin-left: 0rem;
    margin-right: 0rem; }
  img {
    width: 70%;
    margin-top: 10%; } }

.header-main {
  display: block;
  background-image: url(/herosoftime/static/media/header01.7f1b6518.jpg);
  background-size: cover; }

.header-slogan {
  line-height: 33px;
  text-align: left;
  margin: 10px auto;
  display: table;
  padding: 100px 0 50px 0;
  text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.54);
  color: #d87519;
  font-weight: 600;
  font-size: 45px; }

.header-main-categories {
  margin: 0 auto 80px; }

.boxLayer {
  margin: 15px auto 15px auto;
  width: 1175px;
  z-index: 9;
  position: relative; }

.boxTop {
  text-align: center;
  padding: 5px 0 13px 0;
  overflow: hidden;
  box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.08); }

@media (max-width: 820px) {
  .header-slogan h1 {
    font-size: 24px;
    color: #1d1a18;
    margin-left: 1%;
    text-align: center; }
  .header-slogan {
    font-size: 24px;
    color: #1d1a18;
    text-align: center; } }

@media (max-width: 321px) {
  .header-main {
    background-image: url(/herosoftime/static/media/header01.7f1b6518.jpg);
    background-repeat: no-repeat;
    background-size: cover; }
  .header-slogan h1 {
    font-size: 24px;
    color: #1d1a18;
    margin-left: 1%;
    text-align: center; } }

.componentStyle {
  margin-top: 100px;
  margin-top: 2rem;
  padding: 2em 2em 2em 2em;
  font-family: morl-medium;
  position: relative;
  width: 100%;
  min-height: 1px;
  border-style: outset;
  border-width: 7px;
  border-color: coral;
  padding-right: 15px;
  padding-left: 15px; }

.slider {
  font-size: 44px;
  font-weight: 500;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif; }

.title {
  text-align: center; }

.decoration {
  text-align: center;
  /*border: 2px solid $border-color;*/
  padding: 2%;
  width: 90%;
  opacity: 0.7;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease; }

.decoration h3 {
  color: darksalmon; }

.decoration:hover {
  margin-left: -50px;
  width: 100%;
  opacity: 1; }

.secondtitle,
.entry *:not(.fpcontent):not(.qa-faq) .secondtitle,
.entry .secondtitle {
  font-size: 17px;
  color: #444;
  line-height: 25px;
  text-align: center;
  margin: 20px auto 0 auto;
  text-shadow: 2px 2px 1px #fff;
  display: table;
  background: white;
  padding: 10px;
  font-weight: 300;
  border-radius: 5px; }

.item-categories {
  text-align: center;
  display: inline-block;
  margin: auto;
  width: 100%;
  background: #fff;
  border-radius: 3px;
  box-shadow: 1px 1px 3px #ddd;
  padding: 10px; }

.checkboxFive {
  margin: 10px 0 0 0;
  width: auto;
  display: inline-block;
  text-align: left; }

.item-heading {
  font-size: 14px;
  border-bottom: 1px solid #eee;
  padding: 0 5px 5px 0;
  margin-bottom: 15px;
  font-weight: 800;
  color: #2098ce; }

.item-categories .checkboxFive {
  width: auto; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

button,
input {
  overflow: visible; }

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit; }

label {
  display: inline-block;
  margin-bottom: 0.5rem;
  margin-right: 2rem;
  padding-left: 0.5 rem; }

@media (max-width: 800px) {
  .checkboxFive {
    margin: 10px 0 0 0;
    width: auto;
    display: block; }
  .item-categories {
    border-radius: 3px;
    box-shadow: none;
    padding: 10px; } }

.bg-bluepurple {
  background: #af9fbe;
  color: #fff;
  width: 100%;
  height: 400px; }

.section-5 {
  padding: 60px 0px 50px 0px; }

.main-wrap {
  margin: 3%;
  padding: 2%;
  height: inherit;
  text-align: center; }

.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px; }

.fa:hover {
  opacity: 0.7; }

.fa-facebook {
  background: #3b5998;
  color: white; }

.contact {
  font-size: 18px; }

